.main{
    display: flex;
    flex-direction: column;
    justify-content: center;
    &-divle{
        display: flex;
        justify-content: center;
        gap: 150;
        width: 100%;
        &--title{
            width: 33%;
            font-size: 3.5em;
            font-weight: bold;
        }
    }
}

.settings{
    width: 33%;
    height: 500px;
    background-color: white;
    padding: 1rem;
    border-radius: 15px;
    &-main{
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &-title{
        font-size: 1.5em;
        font-weight: bold;
        margin-bottom: 1rem;
    }

    &-form{
        display: flex;
        flex-direction: column;
    }

    &-number{
        width: 10%;
        border-radius: 6px;
        border: 1px solid #ccc;
        text-align: right;
    }
}

.duration{
    display: flex;
    gap: 3;
    margin-bottom: 2rem;
    margin-top: 0.5rem;
    padding-bottom: 0.6rem;
    border-bottom: 1px solid #ccc;
}